"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";
import { Menu, X } from "lucide-react";

export default function Navigation() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [authChecked, setAuthChecked] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      setIsAuthenticated(!!data.user);
      setAuthChecked(true);
    });
    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setIsAuthenticated(!!session?.user);
        setAuthChecked(true);
      }
    );
    return () => {
      listener?.subscription.unsubscribe();
    };
  }, []);

  const guestLinks = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/contact", label: "Contact" },
  ];

  const userLinks = [
    { href: "/dashboard", label: "Dashboard" },
    { href: "/receipts", label: "Receipts" },
    { href: "/insights", label: "Insights" },
  ];

  if (!authChecked) return null;

  return (
    <nav className="bg-white/95 backdrop-blur-sm border-b border-slate-200 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-2">
            <img
              src="/icon-192x192.png"
              alt="Optifi Logo"
              className="h-8 w-8"
            />
            <div className="text-2xl font-bold accent-text">Optifi</div>
          </Link>

          {/* Guest: Burger menu on mobile only */}
          {!isAuthenticated && (
            <>
              <div className="md:hidden">
                <button
                  onClick={() => setIsOpen(true)}
                  className="p-2 rounded-md text-slate-600 hover:bg-slate-100 focus:outline-none"
                  aria-label="Open menu"
                >
                  <Menu className="h-6 w-6" />
                </button>
              </div>
              {/* Sidebar for mobile burger menu */}
              {isOpen && (
                <>
                  <div
                    className="fixed inset-0 z-40 bg-black bg-opacity-30 md:hidden"
                    onClick={() => setIsOpen(false)}
                  />
                  <aside
                    className={`fixed top-0 left-0 h-full w-64 bg-white border-r border-slate-200 shadow-lg flex flex-col z-50 transition-transform duration-300
                    ${isOpen ? "translate-x-0" : "-translate-x-full"}`}
                  >
                    {/* Close button */}
                    <button
                      className="absolute top-4 right-4 text-slate-500 text-2xl focus:outline-none"
                      onClick={() => setIsOpen(false)}
                      aria-label="Close menu"
                    >
                      <X className="h-6 w-6" />
                    </button>
                    <nav className="flex-1 flex flex-col gap-2 mt-16 px-4">
                      {guestLinks.map((link) => (
                        <Link
                          key={link.href}
                          href={link.href}
                          className="flex items-center px-4 py-3 rounded-lg text-base font-medium text-slate-700 hover:bg-slate-50 hover:text-slate-900 mb-1"
                          onClick={() => setIsOpen(false)}
                        >
                          {link.label}
                        </Link>
                      ))}
                    </nav>
                  </aside>
                </>
              )}
              {/* Hide guest links on desktop */}
              <div className="hidden md:flex items-center space-x-6" />
            </>
          )}

          {/* Logged-in: Always show top nav links */}
          {isAuthenticated && (
            <div className="flex items-center space-x-6">
              {userLinks.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className="text-sm font-medium text-slate-700 hover:text-slate-900"
                >
                  {link.label}
                </Link>
              ))}
            </div>
          )}
        </div>
      </div>
    </nav>
  );
}
